
hr {
  min-width: 90%;
  margin: 2.5% 0;
}

p {
  color: whitesmoke;
  margin: 0 5% 2.5% 5%;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
}

.link {
  color: #61dafb;
}

.logo {
  height: 40vmin;
  pointer-events: none;
}

#social-footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 86px;
}

#social-footer>a>img {
  width: 15%;
}
