
.sidebar {
  display: flex;
  flex-direction: column;

  width: 300px;
  max-width: 300px;
  height: calc(100vh - 90px);
  max-height: calc(100vh - 90px);
  filter: drop-shadow(50 1 50 black);

  overflow-y: auto;
  overflow-x: hidden;
}

.sidebar-container {
  background-color: #242830;
}

.sidebar-toggle {
  cursor: pointer;
  font-size: 18px;

  width: 100%;
  min-width: 42px;
  height: 42px;

  border: 0 solid transparent;
  border-bottom: 1px solid rgb(94, 94, 94);
  color: white;
  background-color: #242830;
}

.title {
  user-select: none;
  pointer-events: none;
  color: rgb(198, 201, 207);
  text-align: left;
  padding: 48px 0 16px 8px;

  border: 1px solid rgb(192, 192, 192);
  border-top: 0;
  border-left: 0;
}

.item {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  font-size: calc(10px + 1.25vmin);
  color: rgb(229, 232, 238);
  text-align: left;
  padding: 4px 32px;

  border: 1px solid rgb(163, 163, 163);
  border-top: 0;
  border-left: 0;
}

.item:hover {
  background-color: rgba(141, 141, 141, 0.5);
}

.doc-container {
  width: 900%;
  min-height: calc(100vh - 48px);
  max-height: calc(100vh - 48px);
  overflow-y: auto;
  overflow-x: auto;
}

.doc-content {
  min-height: 65%;
  margin: 0 5% 5% 5%;
  padding: 2.5%;
  border-radius: 15px;
  background-color: white;
}

.doc-content>h1,
.doc-content>h2,
.doc-content>h3,
.doc-content>h4,
.doc-content>h5,
.doc-content>h6 {
  margin: 2.5% 2.5%;
  text-align: left;
}

.doc-content>p {
  color: black;
  font-size: medium;
  margin: 2% 5% 2% 5%;
  width: 90%;
  max-width: unset;
  text-align: left;
  align-content: center;
}

.doc-content>p>img {
  width: 80%;
  padding: 32px 10%;
}

.footer {
  font-size: small;
  color: rgb(229, 232, 238);
  margin: auto auto 16px auto;
}

.footer:hover {
  cursor: pointer;
  text-decoration: underline;
}

.download-btn {
  display: flex;
  width: 248px;
  height: 64px;

  font-size: 18px;
  margin: 64px auto 64px auto;
  justify-content: center;
  align-items: center;

  border: 2px solid black;
  background-color: #00695C;
  border-radius: 5px;
  color: white;
  text-decoration: none;
}

.download-btn>p {
  margin-left: 0;
  margin-right: auto;
}

.download-btn>img {
  margin-left: 16px;
  margin-right: auto;
}
