
#E-Panel-Login {
  display: flex;
  flex-direction: row;
  overflow-y: auto;
  overflow-x: hidden;
  width: 100vw;
  height: calc(100vh - 48px);
}

#E-Panel-Login-Left {
  display: flex;
  user-select: none;
  pointer-events: none;

  flex-direction: column;
  justify-content: center;
  align-items: center;

  min-width: 50vw;
  max-width: 50vw;
  min-height: 114.3vh;
  max-height: 100vh;

  margin-top: -10%;
  margin-bottom: -5%;

  border-right: 2px solid rgb(32, 32, 32);
  background-color: #242830;
}

#E-Panel-Login-Left>img {
  margin-bottom: 5%;
}

#E-Panel-Login-Right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  min-width: 50vw;
  max-width: 50vw;
  min-height: 100vh;
  max-height: 100vh;

  margin-top: -5%;
  margin-bottom: -2.5%;
}

#E-Panel-Login-Right>h1 {
  margin: 5% 0;
  padding-top: 5%;
}

#E-Panel-Login-Right>div {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 50%;
  margin-top: 5%;
}

#E-Panel-Login-Right>div>button {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  min-width: 148px;
  padding: 16px;

  color: rgb(168, 114, 64);
  border-radius: 6px;
  border: 2px solid rgb(168, 114, 64);
  background-color: #f0f0f0;
}

#E-Panel-Login-Right>div>button:hover {
  color: #f0f0f0;
  background-color: rgb(168, 114, 64);
}

#E-Panel-Login-Right>input {
  z-index: 1;

  width: 50%;
  max-height: 42px;
  min-height: 42px;
  margin: 0 auto 16px auto;
  padding: 16px 16px 0 16px;
  border: 0 solid #fff;
  inset: none;

  border-radius: 6px;
  -webkit-appearance: none;
  -webkit-border-radius: 6px;
}

#E-Panel-Login-Right>p {
  z-index: 2;
  user-select: none;
  pointer-events: none;

  width: 50%;
  height: 32px;
  margin: 16px 0 -36px 0;
  padding: 0 8px 0 8px;

  color:rgb(85, 85, 85);
  border-radius: 6px;
}

#E-Panel-Login-Right>p>img {
  user-select: none;
  pointer-events: none;
  margin: 0 12px -2px 0;
}

#E-Panel-Login-Right>a {
  margin: 5% 0 -10% 0;
  padding-bottom: 10%;
}

.login-error {
  text-align: center;
  padding: 8px 0 0 0 !important;
  margin: 0 auto 2% auto !important;
  color: white !important;
  background-color: red;
}

@media screen and (max-width: 1250px) {
  #E-Panel-Login { flex-direction: column !important; }
  #E-Panel-Login-Left { display: none !important; }
  #E-Panel-Login-Right { min-width: 100vw !important; }
}

@media screen and (max-width: 620px) {
  #E-Panel-Login-Right>h1 { padding-top: unset; }
  #E-Panel-Login-Right>div { flex-direction: column; }
  #E-Panel-Login-Right>div>button { margin-bottom: 32px; }
}
