
.display-box-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  width: 100%;
}

.display-box {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 15%;
  height: 15%;
  cursor: pointer;
}

.display-box>p {
  font-family: Ubuntu, sans-serif;
}

.display-box>img {
  user-select: none;
}

.display-box:hover {
  background-color: rgba(105, 105, 105, .5);
}
